import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router, RouterModule } from '@angular/router';
import { User } from '@biz-nest/auth-client';
import {
  AppNameService,
  EventbusService,
  ListComposeAdminPanel,
  MainBarTitleService, WindowService,
  scrollToTop
} from '@front-shared-lib';
import { navByBus } from '@main-shared-lib';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { AdminDateFilterFieldComponent } from '../../misc/admin-date-filter-field/admin-date-filter-field.component';
import { AdminModalComponent } from '../../misc/admin-modal/admin-modal.component';
import { AdminPageContentComponent } from '../../misc/admin-page-content/admin-page-content.component';
import { AdminSearchFieldComponent } from '../../misc/admin-search-field/admin-search-field.component';
import { AdminTableComponent } from '../../misc/admin-table/admin-table.component';
import { AdminTopBarComponent } from '../../misc/admin-top-bar/admin-top-bar.component';
import { UserCardsComponent } from '../user-cards/user-cards.component';

@Component({
  selector: 'app-user-list',
  standalone: true,
  imports: [
    RouterModule,
    AdminModalComponent,
    UserCardsComponent,
    AdminPageContentComponent,
    AdminTableComponent,
    AdminSearchFieldComponent,
    AdminDateFilterFieldComponent,
    AdminTopBarComponent
  ],
  templateUrl: './user-list.component.html',
  styleUrl: './user-list.component.scss'
})
export class UserListComponent
implements OnInit, OnDestroy {
  dest$ = new Subject();
  cleanup?: () => void;
  listCompose = new ListComposeAdminPanel<User>(
    User,
    this.eventbus,
    this.windowService,
    this.toastr
  );

  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;

  actions = [

    {
      label: 'View',
      icon: 'visibility',
      event: (user: User) => {
        this.manageUser(user._id);
      }
    },
    {
      label: 'Delete',
      icon: 'delete',
      event: (user: User) => {
        this.listCompose.openDeleteOneModal(
          user._id,
          'Delete User',
          'Are You Sure You Want To Delete This User?'
        );
      }
    }
  ];

  constructor(
    private windowService: WindowService,
    private toastr: ToastrService,
    private router: Router,
    private eventbus: EventbusService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) { }

  @HostListener('document:click', ['$event'])
  onDocumentClick() {
    this.listCompose.currentActivePopupId = '';
  }

  ngOnInit(): void {
    this.mainBarTitle.title = 'Users - Manage Users';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: `Easily manage and view user accounts for your
      organization to ensure effective oversight and engagement.`
    });
    this.listCompose.getDataArr();
    scrollToTop(this.windowService);
  }

  editUser(user: User) {
    this.navigateByBus(['/profile/' + user.urId], 'viewUser', user);
  }

  sendMessage(user: User) {
    this.navigateByBus(['application/chat'], 'initChat', {
      reqRoom: true,
      userId: user._id
    });
  }

  manageUser(id: string) {
    this.router.navigate(['/manage-user/' + id]);
  }

  determineUrl(user: User) {
    if (user.profilePicture) {
      return user.profilePicture.url;
    }

    return 'assets/imgs/person.png';
  }

  ngOnDestroy(): void {
    this.listCompose.cleanup?.();
  }
}

