import { NgClass } from '@angular/common';
import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { RouterModule } from '@angular/router';
import { User } from '@biz-nest/auth-client';
import { FrontendLogger, IfilterProps } from '@biz-nest/core-client';
import { CurrencyService, TooltipComponent } from '@front-shared-lib';

@Component({
  selector: 'app-user-cards',
  standalone: true,
  imports: [
    RouterModule,
    NgClass,
    TooltipComponent
  ],
  templateUrl: './user-cards.component.html',
  styleUrl: './user-cards.component.scss'
})
export class UserCardsComponent
implements OnInit {
  @Output() filterBy = new EventEmitter<IfilterProps>();
  totalUsers = 0;
  dueUsers = 0;
  unverfiedUsersCount = 0;
  recent = 0;
  totalUsersGrowth = 0;

  constructor(public currencyService: CurrencyService) { }

  ngOnInit(): void {
    this.getTotalUsers();
    this.getRecent();
    this.getUnverifiedUsersCount();
    this.getTotalUsersGrowthDiff();
  }

  async getTotalUsers() {
    try {
      const { count } = await User.filterAll({
        returnEmptyArray: true
      });

      this.totalUsers = count;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }


  async getRecent() {
    try {
      const { count } = await User.filterAll({
        comparisonFilter: [{
          field: 'createdAt',
          fieldValue: new Date(new Date().getFullYear(), new Date().getMonth(), 1),
          operator: 'gte'
        }],
        returnEmptyArray: true
      });

      this.recent = count;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getUnverifiedUsersCount() {
    try {
      const { count } = await User.filterAll({
        propFilter: {
          verified: false
        },
        returnEmptyArray: true
      });

      this.unverfiedUsersCount = count;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getTotalUsersGrowthDiff() {
    try {
      const lastMonth = await User.filterAll({
        comparisonFilter: [
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth() - 1, 1),
            operator: 'gte'
          },
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth(), 0),
            operator: 'lte'
          }
        ]
      });
      const thisMonth = await User.filterAll({
        comparisonFilter: [
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth(), 1),
            operator: 'gte'
          },
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth() + 1, 0),
            operator: 'lte'
          }
        ]
      });

      this.totalUsersGrowth = ((thisMonth.count - lastMonth.count) / (lastMonth.count || 1) * 100) || 0;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }
}

